import React, { Component } from 'react'
import './home.css'
export default class Home extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       user: ''
    }
  }
  
  render() {
    const { message, sendUser } = this.props
    const { user } = this.state
    let alert
    if (message === 'lots') {
      alert = `User "${window.location.pathname.substr(7)}" has no lots!`
    }

    return (
      <div id="home">
        <div id="message">
          <h1>Builds</h1>
          <p id="alert">{alert}</p>
          <p>Enter a username to visit a profile:</p>
          <input
            type="text"
            onChange={e => this.setState({ user: e.target.value })}
          ></input>
          <button onClick={e => sendUser(user)}>submit</button>
        </div>
      </div>
    )
  }
}
